import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CampaignService } from '../../../services/campaign.service'
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { interval, Subscription } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.scss']
})
export class DialogReworkComponent implements OnInit {

  avlListArray: any[] = [];
  avlDisArray: any[] = [];
  allDisArray: any[] = [];
  selctedListArray: any[] = [];
  selctedDisArray: any[] = [];
  reworkTemplateArray: any[] = [];
  dispositionIdsArray: any[] = [];
  dispositionItemsArray: any[] = [];
  reworkCampaignID!: number
  reworkPreviewArray: any[] = [];
  reworkResultArray: any[] = [];
  reworkPreviewMessage: string = ''
  reworkObj: any
  searchList = new FormControl();
  listControl = new FormControl();
  listValue: any

  searchDisposition = new FormControl();
  dispositionControl = new FormControl();
  dispositionValue: any

  filteredList: any[] = [];
  filterListOption: string = 'all';
  searchListValue: string = '';

  filteredDispo: any[] = [];
  filterDispoOption: string = 'all';
  searchDispoValue: string = '';

  isLoadingList: boolean = true
  isLoadingDispo: boolean = true
  isLoadingPreview: boolean = true
  isLoadingResult: boolean = true

  checked = false;
  range = new FormGroup({
    start: new FormControl(),
    end: new FormControl()
  });

  reworkInterval!: Subscription;


  constructor(public dialogRef: MatDialogRef<DialogReworkComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private campaignService: CampaignService, private _formBuilder: FormBuilder, private _snackBar: MatSnackBar) { }

  ngOnInit(): void {
    this.campaignService.getAllCampaignLists(this.data.campaignID).subscribe((res) => {
      this.avlListArray = res
      this.isLoadingList = false
      this.updateList()
    })

    this.campaignService.reworkTemplates().subscribe((res) => {
      this.reworkTemplateArray = res
    })
  }

  selectedItems(item: any, index: number, type: String){
    switch (type) {
      case 'List':
        this.selctedListArray.unshift(item)
        this.avlListArray = this.avlListArray.filter(obj => obj.listName !== item.listName);
        this.filteredList = this.filteredList.filter(obj => obj.listName !== item.listName);
        this.searchListValue = ''
        this.updateList()
        break;
      case 'Disposition':
        this.selctedDisArray.unshift(item)
        this.avlDisArray = this.avlDisArray.filter(obj => obj.dispositionName !== item.dispositionName);
        this.filteredDispo = this.filteredDispo.filter(obj => obj.dispositionName !== item.dispositionName);
        this.dispositionItemsArray.unshift(item)
        this.searchDispoValue = ''
        this.updateDispo()
        break;
      default:
        break;
    }
  }

  availableItems(item: any, index: number, type: String) {
    switch (type) {
      case 'List':
        this.avlListArray.unshift(item)
        this.selctedListArray.splice(index, 1)
        this.searchListValue = ''
        this.updateList()
        break;
      case 'Disposition':
        this.avlDisArray.unshift(item)
        this.selctedDisArray.splice(index, 1)
        this.dispositionItemsArray.splice(index, 1)
        this.searchDispoValue = ''
        this.updateDispo()
        break;
      default:
        break;
    }
  }

  

  onSelectionChange(event: any){
    this.reworkCampaignID = event.value.reworkTemplateId;
    
    this.selctedDisArray = []
    this.avlDisArray = this.filterAvlArray(event.value, this.allDisArray)

    let result = (
      (array, ids) => array.filter(({ dispositionId }) => !ids.includes(dispositionId)))(this.allDisArray, this.avlDisArray.map(({ dispositionId }) => dispositionId)
    );

    this.selctedDisArray = result.concat(this.selctedDisArray)
    this.updateDispo()
  }

  filterAvlArray(value: { dispositionIds: string; }, avlDisArray: any[]){
    this.dispositionIdsArray = value.dispositionIds.split(',').map((x: string | number) => +x);
    const toDelete = new Set(this.dispositionIdsArray);
    return avlDisArray.filter(obj => !toDelete.has(obj.dispositionId)); 
  }

  close(): void {
    this.dialogRef.close();
  }

  async updateList() {
    this.filteredList = this.filterListByOption(this.searchListByValue(this.avlListArray));
  }

  searchListByValue(items: any[]) {
    return items.filter((item: { listName: string; }) => {
      if (this.searchListValue.trim() === '') {
        return true;
      } else {
        return item.listName.toLowerCase().includes(this.searchListValue.trim().toLocaleLowerCase());
      }
    })
  }

  filterListByOption(items: any[]) {
    return items.filter((item: any) => {
      if (this.filterListOption === 'all' || this.filterListOption === item.listName) {
        return true;
      } else return false
    })
  }

  async updateDispo() {
    this.filteredDispo = this.filterDispoByOption(this.searchDispoByValue(this.avlDisArray));
    console.log(this.filteredDispo, "this.filteredDispo");
    
  }

  searchDispoByValue(items: any[]) {
    return items.filter((item: { dispositionName: string; }) => {
      if (this.searchDispoValue.trim() === '') {
        return true;
      } else {
        return item.dispositionName.toLowerCase().includes(this.searchDispoValue.trim().toLocaleLowerCase());
      }
    })
  }

  filterDispoByOption(items: any[]) {
    return items.filter((item: any) => {
      if (this.filterDispoOption === 'all' || this.filterDispoOption === item.dispositionName) {
        return true;
      } else return false
    })
  }
  
  setIndex(event: { selectedIndex: any; }) {
    switch (event.selectedIndex) {
      case 1:
        if (!this.avlDisArray.length) this.getAllCampaignDispositions()
        break;
      case 3:
        if (!this.reworkPreviewArray.length) this.getReworkData()
        break;
      case 4:
        if (!this.reworkResultArray.length) this.startReworkProcess()
        break;
      default:
        break;
    }
  }

  getAllCampaignDispositions(){
    this.campaignService.getAllCampaignDispositions(this.data.campaignID).subscribe((res) => {
      this.isLoadingDispo = false
      this.allDisArray = res.slice(0);
      this.avlDisArray = res.slice(0);
      this.updateDispo()
    })
  }

  
  getReworkData() {
    this.reworkObj = {
      customerID: localStorage.getItem('customerID'),
      userID: localStorage.getItem('userID'),
      userCollectedData: {
        reworkCampaignID: this.data.campaignID,
        reworkIDList: this.selctedListArray.map(x => x.listId),
        dispositionIDList: this.selctedDisArray.map(x => x.dispositionId),
        enableTimeRework: this.checked,
        reworkStartTime: this.range.controls.start.value?.toISOString(),
        reworkStopTime: this.range.controls.end.value?.toISOString()
      }
    }
    this.isLoadingPreview = true
    this.campaignService.getReworkRecordCount(this.reworkObj).subscribe(res => {
      if (res.baseResult.result === "Success") {
        this.isLoadingPreview = false
        this.reworkPreviewArray = res.baseResult.reworkStatusItem
        this.reworkPreviewMessage = res.baseResult.message
      }
    })
  }

  startReworkProcess(){
    this.campaignService.startReworkProcess(this.reworkObj).subscribe(res => {
      if (res.baseResult.result === "Success") {
        this._snackBar.open('Request Sent', '', {
          horizontalPosition: 'center',
          verticalPosition: 'top',
          duration: 3000,
        });
        this.getReworkStatus()
        this.reworkInterval = interval(2000).subscribe(() => {
          this.getReworkStatus()
        });
      }
    })
  }

  getReworkStatus(){
    this.isLoadingResult = true
    this.campaignService.getReworkStatus(this.reworkObj).subscribe(res => {
      if (res.baseResult.result === "Success") {
        if (!res.baseResult.isReworking) { 
          this.isLoadingResult = false
          console.log(res);
          console.log(this.reworkObj);
          
          this.reworkInterval.unsubscribe()
         }
        this.reworkResultArray = res.baseResult.reworkStatusItem
      }
    })
  }
}
