import { Component, ViewChild, OnInit, OnDestroy } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { interval, Subscription } from 'rxjs';
import { CAMPAIGN_STATS_INTERVAL } from './../../constants/constants'
import { DispositionService } from 'src/app/services/disposition.service';
import { ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-dispositions',
  templateUrl: './dispositions.component.html',
  styleUrls: ['./dispositions.component.scss']
})
export class DispositionsComponent implements OnInit, OnDestroy {


  isLoading: boolean = true
  isDisplayList: boolean = false
  isFiltered: boolean = false
  filteredValue: string = ''
  campaignID: number = 0
  dataObj: any;
  combinedObj: any = [];
  displayedColumns: any = [];
  initialColumns: any = [];
  columnsToDisplay: any = this.displayedColumns.slice();
  dispositionInterval!: Subscription;
  dataSource!: MatTableDataSource<any>;
  dropdownList = [];

  selectedItems: any = [];
  extraColumns: any = [];
  displayList: any = [];

  dropdownSettings = {
    singleSelection: true,
    idField: 'campaignId',
    textField: 'name',
    disabledField: 'item_disable',
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    itemsShowLimit: 1,
    allowSearchFilter: true,
    value: 'all',
    closeDropDownOnSelection: true
  };

  displaySettings: any

  singleSelectedItems = [];
  displayItems = [];

  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(private dispositionService: DispositionService, private cd: ChangeDetectorRef) { }

  ngOnInit(): void {
    console.log("in")
    var start = new Date();
    start.setHours(0, 0, 0, 0);

    var end = new Date();
    end.setHours(23, 59, 59, 999);

    this.dataObj = {
      "customerID": this.getItem('customerID'),
      "userID": this.getItem('userID'),
      "campaignID": this.campaignID,
      "startDate": start.toISOString(),
      "endDate": end.toISOString()
    }

    this.getAllAgents()
    this.getAllCampaigns()
    this.getAgentsLive()
  }

  getAgentsLive() {
    this.dispositionInterval = interval(CAMPAIGN_STATS_INTERVAL).subscribe(() => {
      this.getAllAgents()
      this.getAllCampaigns()
    });
  }

  ngAfterViewInit() { }

  getAllAgents() {
    this.dispositionService.getAgentDispositionStats(this.dataObj).subscribe(res => {
      this.setData(res.baseResult)
    }, (err) => {
      this.isLoading = true
      console.log(err)
    })
  }

  getAllCampaigns() {
    this.dispositionService.getAllCampaigns().subscribe(res => {
      if (!this.isFiltered) {
        this.dropdownList = res
        console.log("res");
      }
      
    }, (err) => {
      console.log(err)
    })
  }

  setData(baseResult: any) {
    this.isLoading = false
    this.combinedObj = []
    for (let i of baseResult) this.combinedObj.push(i.dispositionStat)
    if(!this.isFiltered){
      this.combinedObj[0] ? this.displayedColumns = Object.keys(this.combinedObj[0]) : this.displayedColumns = ["Name", "Total Calls", "CallBack", "NoCall"]
    }
    this.dataSource = new MatTableDataSource(this.combinedObj)
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.filteredValue ? this.dataSource.filter = this.filteredValue.trim().toLowerCase() : this.dataSource.filter = ''
    if (!this.isFiltered) this.getDisplayItems(this.displayedColumns)
  }

  getDisplayItems(displayedColumns: string[]){
    this.initialColumns = []
    this.displayList = []
    this.extraColumns = []
    this.selectedItems = []

    for (let i of displayedColumns) {
      this.displayList.push({ val: i, text: i })
      this.selectedItems.push(i)
      this.extraColumns.push(i)
    }
    
    this.displaySettings = {
      singleSelection: false,
      idField: 'val',
      textField: 'text',
      disabledField: 'item_disable',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 1,
      allowSearchFilter: false
    };

    this.initialColumns = this.displayedColumns.slice(0, 4)
    this.displayList = this.displayedColumns.slice(4, this.displayedColumns.length)
    this.extraColumns = this.extraColumns.slice(4, this.extraColumns.length)
    this.selectedItems = this.selectedItems.slice(4, this.selectedItems.length)
    this.isDisplayList = true
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.filteredValue = filterValue
    this.dataSource.filter = filterValue.trim().toLowerCase()

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  public onSingleItemSelect(item: any) {
    this.isLoading = true
    this.dataObj['campaignID'] = item.campaignId
    this.resetInterval()
    this.getAgentsLive()
  }

  public onItemSelect(item: any) {
    this.isFiltered = true
    this.displayedColumns.push(item);
  }

  public onDeSelect(item: any) {
    this.isFiltered = true
    var index = this.displayedColumns.indexOf(item);
    if (index !== -1) {
      this.displayedColumns.splice(index, 1);
    }
  }

  public onSelectAll(items: any) {
    this.isFiltered = true
    this.displayedColumns = this.initialColumns.concat(this.extraColumns)
  }

  public onDeSelectAll(items: any) {
    this.isFiltered = true
    this.displayedColumns = this.initialColumns
  }

  resetInterval() {
    this.dispositionInterval.unsubscribe()
    this.getAgentsLive()
  }

  getItem(key: string) {
    return localStorage.getItem(key)
  }

  camelize(str: string) {
    return str.replace(/\W+(.)/g, function (match, chr) {
      return chr.toUpperCase();
    });
  }

  ngOnDestroy(){
    
  }
}
